import CountDown from '@/componets/uni/uni-countdown.vue'
export default {
  name: 'homeActivity',
  props: {
	  list: Array,
	  time: 0
  },
  components: {
	  CountDown
  },
  data () {
    return {
      releaseDays: 0
    }
  },
  methods:{
	 seeMore () {
		 this.$push('/pages/activity/ActivityList')
	 },
	 activityItemClick (item) {
		 this.$push(this.$mRouter.activityDetail({
			 s: item.scenic_id,
			 g: item.goods_id,
			 p: item.assist_id
		 }))
	 }
  }
}
